<script>
import Gallery from './Gallery.vue';

export default {
  name: 'Home',
  data() {
    return {
      galleryImages1: [
        { src: '/external/untitled116552-j7bb-1700w.png', alt: 'Image6547' },
        { src: '/external/IMG_20170807_111400.png', alt: 'Service 21' },
      ],
      galleryImages2: [
        { src: '/external/image6547-0eu-1700w.png', alt: 'Service 30' },
        { src: '/external/IMG_20181224_210912.png', alt: 'Service 29' },
      ],
      zoomElements: new Map(), // Use Map to store zoom states for each element
      isMoving: false,
      isVisible: false,
      observer: null,
      handscrollScale: 1, // Store the current scale from handscroll
      currentHoveredElement: null, // Store the currently hovered element
      currentImageIndex1: 0, // Initial image index for the first gallery
      currentImageIndex2: 0, // Initial image index for the second gallery
    };
  },
  mounted() {
    // Setup Intersection Observer
    this.observer = new IntersectionObserver((entries, observer) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('visible');
        }
      });
    }, {
      threshold: 0.1 // Trigger when 10% of the element is in view
    });

    // Observe each fade-in-text and fade-in-image element
    document.querySelectorAll('.fade-in-text, .fade-in-text-top-second, .fade-in-text-slow, .fade-in-text-second, .fade-in-noslide, .fade-in-text-top, .fade-in-image').forEach(el => {
      this.observer.observe(el);
    });

    window.addEventListener('scroll', this.handleScroll);
  },
  beforeDestroy() {
    if (this.observer) {
      this.observer.disconnect();
    }
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    enableZoom(event) {
      const element = event.currentTarget;
      this.zoomElements.set(element, { isZoomEnabled: true });
      this.currentHoveredElement = element; // Store the currently hovered element
      element.style.transition = 'transform 0.6s ease-out'; // Set transition for zooming
      this.applyCombinedScale(element);
    },
    disableZoom(event) {
      const element = event.currentTarget;
      this.zoomElements.set(element, { isZoomEnabled: false });
      element.style.transform = `scale(${this.handscrollScale})`; // Reset to handscroll scale
      element.querySelector('img').style.transform = ''; // Reset image transform
      this.currentHoveredElement = null;
    },
    handleMouseMove(event) {
      const element = event.currentTarget;
      if (!this.zoomElements.get(element)?.isZoomEnabled) return;

      this.isMoving = true;
      const bounds = element.getBoundingClientRect();
      const xPercent = (event.clientX - bounds.left) / bounds.width;
      const yPercent = (event.clientY - bounds.top) / bounds.height;
      const multiplier = 22;
      const xOffset = -(xPercent - 0.5) * multiplier;
      const yOffset = -(yPercent - 0.5) * multiplier;

      element.querySelector('img').style.transform = `translate(${xOffset}px, ${yOffset}px) scale(${this.getCurrentScale(element)})`;

      setTimeout(() => {
        this.isMoving = false;
      }, 200);
    },
    handleScroll() {
      const sections = document.querySelectorAll('.section');
      sections.forEach(section => {
        const bounds = section.getBoundingClientRect();
        const viewportHeight = window.innerHeight;

        // Calculate the exit ratio only
        let exitRatio = Math.max(0, Math.min(1, bounds.bottom / viewportHeight));

        // Linear scale calculation based on exiting only
        let scale = 1.0;
        if (exitRatio <= 1.0 && exitRatio >= 0.4) {
          scale = 0.95 + 0.05 * (exitRatio - 0.4) / 0.6;
        } else if (exitRatio < 0.4) {
          scale = 0.95;
        }

        // Linear inset calculation and conversion to scale
        let inset = 0.0;
        if (exitRatio <= 1.0 && exitRatio >= 0.3) {
          inset = 6.25 * (1.0 - exitRatio) / 0.7;
        } else if (exitRatio < 0.3) {
          inset = 6.25;
        }
        // Convert inset percentage to scale factor
        const insetScale = 1 - inset / 100;

        // Combine the scaling and inset effect
        const combinedScale = scale * insetScale;

        // Store the handscroll scale
        this.handscrollScale = combinedScale;

        // Apply the calculated scale to section objects
        const objects = section.querySelectorAll('.section-object');
        objects.forEach(obj => {
          obj.classList.add('scale'); // Add class to trigger scaling transition
          obj.style.transform = `scale(${combinedScale})`; // Apply combined scale directly
        });

        // Ensure the hovered element maintains the correct scale during scroll
        if (this.currentHoveredElement && this.zoomElements.get(this.currentHoveredElement)?.isZoomEnabled) {
          this.currentHoveredElement.style.transform = `scale(${this.handscrollScale * 1.05})`;
        }
      });
    },
    getCurrentScale(element) {
      return this.zoomElements.get(element)?.isZoomEnabled ? this.handscrollScale * 1.05 : this.handscrollScale;
    },
    applyCombinedScale(element) {
      element.style.transform = `scale(${this.getCurrentScale(element)})`;
    },
    prevImage(galleryIndex) {
      if (galleryIndex === 1) {
        this.currentImageIndex1 = (this.currentImageIndex1 + this.galleryImages1.length - 1) % this.galleryImages1.length;
      } else if (galleryIndex === 2) {
        this.currentImageIndex2 = (this.currentImageIndex2 + this.galleryImages2.length - 1) % this.galleryImages2.length;
      }
    },
    nextImage(galleryIndex) {
      if (galleryIndex === 1) {
        this.currentImageIndex1 = (this.currentImageIndex1 + 1) % this.galleryImages1.length;
      } else if (galleryIndex === 2) {
        this.currentImageIndex2 = (this.currentImageIndex2 + 1) % this.galleryImages2.length;
      }
    }
  }
}
</script>

























