<!-- Gallery.vue -->
<template>
    <div class="gallery-container" 
      @mouseover="enableZoom"
      @mouseleave="disableZoom"
      @mousemove="handleMouseMove"
      :class="{ 'zoom-frame': isZoomEnabled, 'visible': isImageVisible }">
      <div class="carousel-container">
        <img v-for="(image, index) in images" :key="index"
          :class="['carousel-image', { 'zoom-image': isZoomEnabled, 'moving': isMoving, 'visible': isImageVisible }]"
          :src="image.src"
          :alt="image.alt"
          v-if="currentImageIndex === index"
        />
      </div>
      <button @click="prevImage" class="carousel-button prev-button">&lt;</button>
      <button @click="nextImage" class="carousel-button next-button">&gt;</button>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      images: {
        type: Array,
        required: true
      }
    },
    data() {
      return {
        currentImageIndex: 0,
        isZoomEnabled: false,
        isMoving: false,
        isImageVisible: false,
        handscrollScale: 1, // Store the current scale from handscroll
        currentHoveredElement: null // Store the currently hovered element
      };
    },
    methods: {
      enableZoom(event) {
        const element = event.currentTarget;
        this.currentHoveredElement = element; // Store the currently hovered element
        this.isZoomEnabled = true;
        element.style.transition = 'transform 0.6s ease-out'; // Set transition for zooming
        this.applyCombinedScale(element);
      },
      disableZoom(event) {
        const element = event.currentTarget;
        this.isZoomEnabled = false;
        element.style.transform = `scale(${this.handscrollScale})`; // Reset to handscroll scale
        element.querySelector('img').style.transform = ''; // Reset image transform
        this.currentHoveredElement = null;
      },
      handleMouseMove(event) {
        const element = event.currentTarget;
        if (!this.isZoomEnabled) return;
  
        this.isMoving = true;
        const bounds = element.getBoundingClientRect();
        const xPercent = (event.clientX - bounds.left) / bounds.width;
        const yPercent = (event.clientY - bounds.top) / bounds.height;
        const multiplier = 22;
        const xOffset = -(xPercent - 0.5) * multiplier;
        const yOffset = -(yPercent - 0.5) * multiplier;
  
        element.querySelector('img').style.transform = `translate(${xOffset}px, ${yOffset}px) scale(${this.getCurrentScale(element)})`;
  
        setTimeout(() => {
          this.isMoving = false;
        }, 200);
      },
      handleScroll() {
        const sections = document.querySelectorAll('.section');
        sections.forEach(section => {
          const bounds = section.getBoundingClientRect();
          const viewportHeight = window.innerHeight;
  
          // Calculate the exit ratio only
          let exitRatio = Math.max(0, Math.min(1, bounds.bottom / viewportHeight));
  
          // Linear scale calculation based on exiting only
          let scale = 1.0;
          if (exitRatio <= 1.0 && exitRatio >= 0.4) {
            scale = 0.95 + 0.05 * (exitRatio - 0.4) / 0.6;
          } else if (exitRatio < 0.4) {
            scale = 0.95;
          }
  
          // Linear inset calculation and conversion to scale
          let inset = 0.0;
          if (exitRatio <= 1.0 && exitRatio >= 0.3) {
            inset = 6.25 * (1.0 - exitRatio) / 0.7;
          } else if (exitRatio < 0.3) {
            inset = 6.25;
          }
          // Convert inset percentage to scale factor
          const insetScale = 1 - inset / 100;
  
          // Combine the scaling and inset effect
          const combinedScale = scale * insetScale;
  
          // Store the handscroll scale
          this.handscrollScale = combinedScale;
  
          // Apply the calculated scale to section objects
          const objects = section.querySelectorAll('.section-object');
          objects.forEach(obj => {
            obj.classList.add('scale'); // Add class to trigger scaling transition
            obj.style.transform = `scale(${combinedScale})`; // Apply combined scale directly
          });
  
          // Ensure the hovered element maintains the correct scale during scroll
          if (this.currentHoveredElement && this.isZoomEnabled) {
            this.currentHoveredElement.style.transform = `scale(${this.handscrollScale * 1.05})`;
          }
        });
      },
      getCurrentScale(element) {
        return this.isZoomEnabled ? this.handscrollScale * 1.05 : this.handscrollScale;
      },
      applyCombinedScale(element) {
        element.style.transform = `scale(${this.getCurrentScale(element)})`;
      },
      prevImage() {
        this.currentImageIndex = (this.currentImageIndex - 1 + this.images.length) % this.images.length;
      },
      nextImage() {
        this.currentImageIndex = (this.currentImageIndex + 1) % this.images.length;
      }
    }
  }
  </script>
  
  <style scoped>
  .carousel-container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  
  .carousel-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 0.5s ease-in-out;
  }
  
  .carousel-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    z-index: 10;
  }
  
  .prev-button {
    left: 10px;
  }
  
  .next-button {
    right: 10px;
  }
  </style>
  